/*global chrome*/

import React from 'react';
import './App.css';
import SiteOption from './SiteOption.js'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sites: ["youtube.com"],
      newSite: "",
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.removeSite = this.removeSite.bind(this)
  }

  componentDidMount() {
    chrome.storage.sync.get(function(result) {
      console.log(result);
    })
  }

  componentWillUnmount() {
  }

  handleChange(event) {
    this.setState({newSite: event.target.value});
  }

  handleSubmit(event) {
    this.setState({ sites: this.state.sites.concat(this.state.newSite) });
    event.preventDefault();
  }

  removeSite(site) {
    const sites = this.state.sites.filter(siteItem => siteItem !== site);
    this.setState({ sites: sites });
  }

  render() {
    return (
      <div className="wrapper">
        <h1>BW_BROWSER</h1>
        <h2>BLOCKED SITES</h2>
        <button onClick={this.handleSubmit}>+</button>
        <input 
          type="text" 
          placeholder="block site (ex. youtube.com)" 
          value={this.state.value} 
          onChange={this.handleChange}
        />
        {this.state.sites.map(site => (
          <SiteOption removeSite={this.removeSite} site={site}></SiteOption>
        ))}
      </div>
    );
  }
}

export default App;
