import React from 'react';

class SiteOption extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  render() {
    return (
      <div className="site-option">
        <p onClick={() => this.props.removeSite(this.props.site)} className="delete-button">x</p>
        <p>{this.props.site}</p>
      </div>
    );
  }
}

export default SiteOption;
